// Offer Carousel Slideshow

const arrayCarousel = ['/static/images/offers/offer1.jpg',
    '/static/images/offers/offer2.jpg',
    '/static/images/offers/offer3.jpg',
    '/static/images/offers/offer4.jpg',
    '/static/images/offers/offer5.jpg'
];

counter = 0
const setImage = () => {
    document.getElementById("carousel-image").src = arrayCarousel[counter];
    counter = (counter + 1) % arrayCarousel.length;
}

setInterval(setImage, 1000);
